.visual {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0px;

	.bannerbox {
		position: absolute;
		top: 0px;
		width: 100%;
		height: 700px;
		display: block;
		background: url(../img/main3.jpg);
		position: relative;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;

		.inner {
			width: 100%;
			height: 100%;
			background: rgba(0, 0, 0, 0.6);
			position: absolute;

			.text {
				width: 1180px;
				height: 100%;
				margin: 0 auto;
				display: flex;
				justify-content: center;
				flex-direction: column;

				h2 {
					font: bold 15px/1 'arial';
					color: white;
					margin-bottom: 15px;
				}

				h3 {
					font: bold 50px/1.6 'arial';
					color: white;
				}
			}
		}
	}
}
